import React from 'react'
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Cardd from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import ast from '../../1st.png';
import bnd from '../../2nd.png';
import crd from '../../3rd.png';
import dth from '../../4th.png';


export default function Card() {
  return (
    <div>
         <Box container spacing={2}>
      <Grid container spacing={3}>
        <Grid item xs={3}>
           
            <Cardd sx={{ maxWidth: 345,maxHeight:350 }}>
                <img width={"100%"} src={ast} />
   
    </Cardd>
        </Grid>
        <Grid item xs={3}>
        <Cardd sx={{ maxWidth: 345,maxHeight:350 }}>
                <img width={"100%"} src={bnd} />
   
    </Cardd>
        </Grid>
        <Grid item xs={3}>
        <Cardd sx={{ maxWidth: 345,maxHeight:350 }}>
                <img  width={"100%"} src={crd} />
   
    </Cardd>
        </Grid><Grid item xs={3}>
        <Cardd sx={{ maxWidth: 345,maxHeight:350 }}>
                <img  width={"100%"}  src={dth} />
   
    </Cardd>
        </Grid>
      </Grid>
    </Box>
    </div>
  )
}
