import React from 'react'
import { Link } from 'react-router-dom'
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import CinemaFans from './CinemaFans';
import FilmFraternity from './FilmFraternity';

export default function Register() {
  const [value, setValue] = React.useState('1');

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div>

         <Box sx={{ width: '100%', typography: 'body1' }}>
      <TabContext value={value} >
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <TabList onChange={handleChange} aria-label="lab API tabs example">
          <Tab label="Film Fraternity" value="1" style={{ color: "white" }} />
            
              <Tab label="Cinema Fans" value="2" style={{ color: "white" }} />
        
                 </TabList>
        </Box>
        <TabPanel value="1"><FilmFraternity/></TabPanel>
        <TabPanel value="2"><CinemaFans/> </TabPanel>
        
      </TabContext>
    </Box>
   
   
    </div>
  )
}
